.dashboard-container {
  padding: 1em;
  background-color: white;
}

.dashboard-kpi-wrapper{
  padding: 1em;
  border-radius: 3pt;
  background-color: #d2dae2;
  margin-bottom: 0.5em;
  /* border: 1pt solid pink; */
}

.dashboard-kpi-wrapper-green1{
  padding: 1em;
  border-radius: 3pt;
  background: #7F7FD5;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
   margin-bottom: 0.5em;
  /* border: 1pt solid pink; */
}

.dashboard-kpi-wrapper-revenue{
  padding: 1em;
  border-radius: 3pt;
  background: #7F7FD5;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
   
  margin-bottom: 0.5em;
}

.dashboard-kpi-wrapper-pink{
  padding: 1em;
  border-radius: 3pt;
  background: #FF5F6D;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FFC371, #FF5F6D);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #FFC371, #FF5F6D); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  margin-bottom: 0.5em;
}

.dashboard-kpi-wrapper-gray{
  padding: 1em;
  border-radius: 3pt;
  margin-bottom: 0.5em;
  background: #8e9eab;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #eef2f3, #8e9eab);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #eef2f3, #8e9eab); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.dashboard-kpi-header{
  text-align: center;
  /* border: 1pt solid blueviolet; */
  color: white;
  font-weight: bold;
}

.dashboard-kpi-value{
  text-align: center;
  /* border: 1pt solid violet; */
  font-size: 17pt;
  color: white;
}

.dashboard-category{
  margin-top: 1em;
}

.dashboard-category-header{
  font-size: 15pt;
  color: #808e9b;
}

.dashboard-list-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  /* border: 1pt solid pink; */
  margin-top: 0.5em;
}

.dashboard-list-index{
  width: 35px;
  /* border: 1pt solid blueviolet; */
}

.dashboard-list-name{
  /* border: 1pt solid blueviolet; */
  padding-left: 0.5em;
  min-width: 100px;
}

.dashboard-list-value{
  min-width: 150px;
}

.dashboard-list-losses{
  color: #ff5e57;
}

.dashboard-currency-symbol{
  padding-left: 0.3em;
  color: #808e9b;
}