.login-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #1e3c72; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to top,
    #2a5298,
    #1e3c72
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to top,
    #2a5298,
    #1e3c72
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.login-inputs-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding-left: 1.5em;
  padding-right: 1.5em;
  margin-top: 5em;
  margin-bottom: 1em;
}

.login-header {
  font-family: "Audiowide", cursive;
  font-weight: bolder;
  font-size: 17pt;
  color: white;
  margin-top: 1em;
  margin-bottom: 0.5em;
}

.button--login {
  width: 170px;
  background-color: white;
  color: #1e3c72;
}

.button--login-scan {
  background-color: #0fbcf9;
}

.text-input--nicer {
  min-height: 50px;
  border-radius: 3px;
  margin-top: 0.5em;
  font-size: 17pt;
  border-radius: 5px;
  color: white;
}

.login-bottom-panel {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
