.trips-list-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  /* border: 1pt solid palevioletred; */
  width: 100%;  
}

.trips-list-vehicleReg-header {
  width: 100%;
  font-size: 9pt;
  color: #485460;
  /* border: 1pt solid palegreen; */
}

.trips-list-vehicleReg {
  width: 100%;
  font-weight: bold;
  /* border: 1pt solid palegreen; */
}

.trips-list-datetime {
  width: 100%;
  font-size: 10pt;
  color: #485460;
  /* border: 1pt solid peru; */
}

.trips-list-left-wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* border: 1pt solid palevioletred; */
  width: 100%;

}

.trips-list-right-wrapper{
  min-width: 147px;
  height: 100%;
  /* border: 1pt solid palevioletred; */
  margin-right: 0.3em;
}

.trip-list-profit{
  font-size: 10pt;
}

.trip-list-losses{
  font-size: 10pt;
  color: #fc5c65;
}