.view-container {
  background-color: white;
}

.view-list-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* border: 1pt solid palevioletred; */
  width: 100%;
}

.view-list-header {
  /* border: 1pt solid blue; */
  width: 100%;
  font-size: 10pt;
  color: #808e9b;
}

.view-list-value {
  /* border: 1pt solid palegreen; */
  width: 100%;
}

.view-dashboard-container{
  padding: 1em;  
  background-color: whitesmoke;
}

.view-dashboard-item-wrapper{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-top: 0.3em;
  padding-bottom: 0.3em;
  /* border: 1pt solid yellowgreen; */
}

.view-dashboard-item-minus-wrapper{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-left: 0.3em;
  padding-bottom: 0.3em;
  /* border: 1pt solid yellowgreen; */
}

.view-dashboard-item-line{
  padding-bottom: 0.3em;
  border-bottom: 1pt solid slategrey;  
}
.view-dashboard-item-label{

  /* border: 1pt solid yellowgreen; */
